.header {
    width: 100%;
    height: 122px;
    position: absolute;
    top: 0;
    left: 0;
    @media (max-width: $md-2) {
        height: 60px;
        margin-top: 30px;
    }
    // .header__container

    &__container {
        height: 100%;
        max-width: 1700px;
        margin: 0 auto;
        padding: 0 30px;
        @media (max-width: $md-4) {
            padding: 0 15px;
        }
    }

    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .header__left

    &__left {
        display: flex;
        align-items: center;
        height: 100%;
       
    }

    // .header__logo

    &__logo {
        z-index: 3;
        margin-right: 110px;
        @media (max-width: $md-1) {
            margin-right: 0;
        }
    }

    // .header__menu
    &__menu._active {
        left: 0;
        
    }
    &__menu {
        position: relative;
        height: 100%;
        transition: .4s all 0s ease;
        overflow-y: auto;
        @media (max-width: $md-2) {
            position: fixed;
            background-color: $darkBlueColor;
            padding-top: 170px;
            width: 100%;
            height: 100%;
            top: 0;
            left: -100%;
            z-index: 1;
        } 
        @media (max-width: $md-5) {
            padding-top: 130px;
        }
    }

    // .header__items

    &__items {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;
        @media (max-width: $md-2) {
            height: auto;
            flex-direction: column;
            padding: 0;
        }
    }

    // .header__item
 
    &__item {
        position: relative;
        top: -20px;
        list-style: none;
        margin: 0 12px;
        cursor: pointer;
        height: 70%;
        width: 93px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 10px;
        justify-content: center;
        transition: .3s all 0s linear;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        @media (max-width: $md-1) {
            margin: 0 5px;
        } 
        @media (max-width: $md-2) {
            height: auto;
            margin-bottom: 30px;
        }
        @media (max-width: $md-5) {
            margin-bottom: 10px;
        }
        span {
            font-weight: 500;
            font-size: 25px;
            color: $darkBlueColor;
            @media (max-width: $md-2) {
                color: #fff;
                font-size: 35px;
            }
            @media (max-width: $md-5) {
                font-size: 25px;
            }
        }

        &_hidden {
            display: none;
            @media (max-width: $md-2) {
                display: flex;
            }
        }

        // .header__item_active
        &:hover, &_active { 
            @media (max-width: $md-2) {
                background-color: transparent;
            }
            background-color: $blueColor;
            span {
                color: #fff;
            }
        }
    }

    // .header__right

    &__right {
        display: flex;
        align-items: center;
        @media (max-width: $md-2) {
            display: none;
        }
    }

    // .header__user
    &__bell, &__user {
        @include bg("contain");
        width: 42px;
        height: 42px;
        margin-left: 72px;
        @media (max-width: $md-1) {
            margin-left: 22px;
        } 
        cursor: pointer;
    } 

    &__user {
        
    }

    // .header__bell

    &__bell {
      
    }

    // .header__button

    &__button {
        width: 188px;
        height: 76px;
        margin-left: 76px;
        @media (max-width: $md-1) {
            margin-left: 32px;
        }  
    }

    &__burger._active {
        &::before {
            top: 8px;
            transform: rotate(-45deg);
        }
        &::after {
           top: 7px;
           transform: rotate(45deg);
        }
        span {
           display: none;
        }
    }
    &__burger {
        display: none;
        
        @media (max-width: $md-2) {
            display: block;
            width: 35px;
            height: 28px;  
            position: relative;
            z-index: 3;
            cursor: pointer;
            margin-top: 10px;
            &::before,
            &::after {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 2px;
                display: block;
                content: "";
                background-color: $blueColor;
                transition: .3s all 0s linear;
            }
            &::before {
                top: 0;
            }
            &::after {
                top: 16px;
            }
            span {
                position: absolute;
                top: 8px;
                left: 0;
                width: 100%;
                height: 2px;
                background-color: $blueColor;
            }
        }
        
    }
}
