.service {
    width: 100%;
    position: relative;
    // .service__line-left
    &__line-left, &__line-right {
        position: absolute;
        bottom: 0;
        z-index: -1;
        width: 250px;
        @media (max-width: 1600px) {
            width: 200px;
        }
        @media (max-width: $md-3) {
            width: 150px;
        }
        @media (max-width: 600px) {
            width: 100px;
         }
    }

    &__line-left {
        left: 0;
    }

    // .service__line-right

    &__line-right {
        right: 0;
    }

    // .service__container

    &__container {
    }

    // .service__body

    &__body {
        width: 100%;
        height: 100%;
        padding-bottom: 350px;
        @media (max-width: $md-5) {
            padding-bottom: 150px;
        }
    }

    // .service__header

    &__header {
        width: 100%;
        display: flex;
        justify-content: center;
        margin-bottom: 83px;
        @media (max-width: $md-3) {
            margin-bottom: 40px;
        }
    }

    // .service__title

    &__title {
        max-width: 387px;
        text-align: center;
    }

    // .service__content

    &__content {

    }

    // .service__row

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-2) {
            flex-direction: column;
            align-items: center;
        }
    }

    // .service__item

    &__item {
        height: 553px;
        width: 33.3333%;
        padding: 50px 41px 72px 41px;
        margin: 0 18px;
        border-radius: 29px;
        background-color: #fff;
        border-bottom: 3px solid #99BAED;
        display: flex;
        align-items: center;
        flex-direction: column;
        transition: .4s all 0s linear;
    
        @media (max-width: $md-2) {
            width: 80%;
            margin-bottom: 30px;
            padding: 50px 31px 42px 31px;
        }
        @media (max-width: $md-6) {
            width: 100%;
            height: 500px;
        }
        &_active, &:hover, &:focus {
             color:#fff;
             background-color: #99BAED;
             box-shadow: 0 8px 16px 0 #99bbed7e;
             .service__text {
                color:#fff;
                transition: .5s all 0s linear;
             }
        }
    }

    // .service__ico

    &__ico {
        width: 103px;
        min-height: 126px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 59px;
        background-color: #E5ECFE;
        border-radius: 13px;
        border-bottom: 1px solid #008FDD;
      
        span {
            @include bg("contain");
            width: 60px;
            height: 60px;
        }
    }

    // .service__caption

    &__caption {
        font-weight: 700;
        font-size: 45px;
        text-align: center;
        margin-bottom: 40px;
        @media (max-width: $md-6) {
            margin-bottom: 20px;
        }
    }

    // .service__text

    &__text {
        max-width: 360px;
        text-align: center;
    }
}
