*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars
$container: 1400px + 30px;
$md-1: $container;
$md-2: 1039.98px;
$md-3: 991.98px;
$md-4: 767.98px;
$md-5: 479.98px;
$md-6: 399.98px;

$blueColor: #A3C1EF;  
$darkBlueColor: #163561;
$yellowColor: #FBB400; 

// Elemements
.body {
	font-family: "Catamaran", Helvetica, sans-serif;
}
.body._lock {
	overflow: hidden;
}

.container {
	max-width: $container;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.title {
	font-size: 70px;
	font-weight: 700;
	color: $darkBlueColor;
	span {
		color: $yellowColor;
	}
	@media (max-width: $md-4) {
		font-size: 55px;
	}
	@media (max-width: $md-6) {
		font-size: 45px;
	}
}
  
.button {
	@include no-btn();
	background-color: #99BAED;
	color: #fff;
	cursor: pointer;
	font-weight: 700;
	font-size: 20px;
	border-radius: 20px;
	box-shadow: 0 8px 16px 0 #99bbed7a;
	transition: .3s all 0s linear;
	&:hover {
		box-shadow: 0 8px 16px 0 #99bbedd5;
	}
}

.logo {
	display: flex;
	align-items: center;
	// .logo__ico

	&__ico {
		@media (max-width: $md-4) {
			width: 55px;
			height: 65px;
		}
		@include bg("contain");
		width: 75px;
		height: 85px;
	}

	// .logo__text

	&__text {
		@media (max-width: $md-4) {
			width: 71px;
			height: 40px;
		}
		@include bg("contain");
		width: 91px;
		height: 53px;
	}

	
	
}

.text {
	color: #153461;
	font-size: 25px;
	letter-spacing: 1px;
	@media (max-width: $md-4) {
		font-size: 23px;
	 } 
}
