.footer {
    width: 100%;
    position: relative;
    padding-bottom: 112px;
    // .footer__container

    &__container {
    }

    // .footer__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .footer__logo

    &__logo {
        margin-bottom: 36px;
        @media (max-width: $md-4) {
            margin-bottom: 16px;
         }  
    }

    // .footer__text

    &__text {
        max-width: 526px;
        text-align: center;
        margin-bottom: 47px;
        @media (max-width: $md-4) {
           margin-bottom: 27px;
        } 
    }

    // .footer__items

    &__items {
        display: flex;
        align-items: center; 
       justify-content: center;
       width: 80%;
       padding-bottom: 48px;
       border-bottom: 1px solid $blueColor;
       @media (max-width: $md-4) {
        padding-bottom: 0;
        border-bottom: none;
        }
    }

    // .footer__item

    &__item {
        width: 67px;
        height: 67px;
        background-color: #D3E5FF;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        margin: 0 12px;
        transition: .3s all 0s linear;
        @media (max-width: $md-4) {
            width: 57px;
            height: 57px;
        }
        @media (max-width: $md-5) {
           width: 47px; 
           height: 47px;
        }
        &:hover {
            background-color: #99BAED;
        }
        span {
            @include bg("contain");
            width: 25px;
            height:25px;
            @media (max-width: $md-6) {
                width: 20px;
                height: 20px;
            }
        }
    }

    // .footer__image-left
    &__image-left, &__image-right {
        position: absolute;
        bottom: 0;
        z-index: -1;
        @media (max-width: $md-4) {
            width: 150px;
         } 
    }

    &__image-left {
        left: 0;
    }

    // .footer__image-right

    &__image-right {
        right: 0;
    }
}

