.home {
    width: 100%;
    min-height: 100vh;
    padding-top: 150px;
    @media (max-width: $md-5) {
        padding-top: 110px;
    }
    // .home__container

    &__container {
        width: 100%;
        height: 100%;
        max-width: 1730px;
        margin-right: 0;
        margin-left: auto;
        padding: 0 0 0 15px;
    }

    // .home__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column;
            align-items: flex-end;
            justify-content: center;
        }
        @media (max-width: $md-4) {
            justify-content: flex-start;
        }
    }

    // .home__left

    &__left {
        margin-right: 30px;
        @media (max-width: $md-3) {
            margin-bottom: 40px;
            margin-right: 15px;
        }
        
    }

    // .home__title
   
    &__title {
        max-width: 576px;
        margin-bottom: 29px;
        font-size: 75px;

      
        
        @media (max-width: $md-3) {
           margin-bottom: 10px;
           font-size: 70px;
        } 
        @media (max-width: $md-4) {
            font-size: 55px;
        }
        @media (max-width: $md-6) {
            font-size: 45px;
        }
    }
   
   
    // .home__text

    &__text {
        max-width: 526px;
        margin-bottom: 97px;
        @media (max-width: $md-3) {
           margin-bottom: 47px;
         } 
         @media (max-width: $md-4) {
            margin-bottom: 27px;
        }
    }

    // .home__button

    &__button {
        width: 188px;
        height: 77px;
    }

    // .home__image

    &__image {
        width: 50%;
        @media (max-width: $md-3) {
            width: 70%;
        }
        @media (max-width: $md-4) {
            width: 100%;
        }
    }
}
