.history {
    width: 100%;
    position: relative;
    margin-bottom: 150px;
    @media (max-width: $md-5) {
        margin-bottom: 60px;
    }
    // .history__line-left
    &__line-left, &__line-right {
        position: absolute;
        top: 0;
        width: 250px;
        @media (max-width: 1300px) {
            width: 200px;
        }
        @media (max-width: $md-3) {
            width: 150px;
        }
        @media (max-width: 600px) {
           width: 100px;
        }
    }

    &__line-left {
        left: 0;
    }

    // .history__line-right

    &__line-right {
        right: 0;
    }

    // .history__container

    &__container {
        max-width: 1700px + 30px;
        padding: 0 15px;
        height: 100%;
        margin: 0 auto;
        
    }

    // .history__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 200px;
        @media (max-width: $md-3) {
            padding-top: 150px;
            flex-direction: column-reverse;
            align-items: center;
            text-align: center;
        }
    }

    // .history__image

    &__image {
        width: 50%;
        @media (max-width: $md-3) {
            width: 70%;
        }
    }

    // .history__content

    &__content {
        margin-left: 30px;
        @media (max-width: $md-3) {
            margin: 0;
            margin-bottom: 40px;
        }
    }

    // .history__title

    &__title {
        margin-bottom: 80px;
        @media (max-width: $md-3) {
            margin-bottom: 40px;
        }
       
    }

    // .history__text

    &__text {
        max-width: 526px;
        margin-bottom: 48px;
        &_last {
            margin-bottom: 108px;
            @media (max-width: $md-3) {
                margin-bottom: 58px;
            }
        }
    }

    // .history__button

    &__button {
        width: 188px;
        height: 77px;
    }
}
