.gallery {
    width: 100%;
    position: relative;
    margin-bottom: 180px;
    @media (max-width: $md-5) {
        margin-bottom: 80px;
    }
    // .gallery__line-left
    &__line-left, &__line-right {
        position: absolute;
        top: 0;
        z-index: -1;
        width: 250px;
        @media (max-width: 1600px) {
            width: 200px;
        }
        @media (max-width: $md-3) {
            width: 150px;
        }
        @media (max-width: 600px) {
            width: 100px;
        }
    }

    &__line-left {
        left: 0;
    }

    // .gallery__line-right

    &__line-right {
        right: 0;
    }

    // .gallery__container

    &__container {
    }

    // .gallery__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 320px;
        @media (max-width: $md-4) {
            padding-top: 250px;
        }
        @media (max-width: $md-5) {
           padding-top: 150px;
        }
    }

    // .gallery__title

    &__title {
        margin-bottom: 110px;
        @media (max-width: $md-4) {
            margin-bottom: 60px;
        }
        text-align: center;
    }

    // .gallery__content

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 77px;
        @media (max-width: $md-4) {
            margin-bottom: 40px;
            flex-direction: row;
            justify-content: center;
        }

    }

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    // .gallery__image

    &__image {
        width: 435px;
        height: 435px;
        margin: 15px;
        position: relative;
        z-index: 1;
        @media (max-width: $md-1) {
            width: 310px;
            height: 310px;
            margin: 10px;
        }
        @media (max-width: $md-2) {
            width: 290px;
            height: 290px;
            margin: 10px;
        }
        @media (max-width: $md-3) {
            width: 220px;
            height: 220px;
            margin: 10px;
        }
        @media (max-width: $md-4) {
            width: 200px;
            height: 200px;
        }
        @media (max-width: $md-5) {
            width: 90%;
            height: auto;
         }
    }

    // .gallery__button

    &__button {
        width: 188px;
        height: 77px;
    }
}
